      <div class="spacer"></div>
  </article>
</template>

<script>
export default {
  name: 'BottomSection',
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
@import '../assets/scss/variables';

.bottom-section{
  height: 400px;
}
.content, .spacer{
  height: 50%;
}
.content{
  position: relative;
  &__title{
    position: absolute;
    right: 5%;
    top: 45%;
    color: #fff;
    font-size: 1.8rem;
  }
  &__img{
    height: 100%;
    width: auto;
    margin-left: 5%;
    position: absolute;
    top: 30%;

  }
}
.spacer{
  background-color: $yellow;
}
</style>


